import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { fetchChangePassword } from '../../store/slices/authSlice';
import { ButtonGreen } from '../ButtonGreen';
import { Input } from '../Input';
import { PopupWindow } from '../PopupWindow';


export const PasswordEdit = () => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [onfirmPasswordError, setConfirmPasswordError] = useState(false);
  const [onfirmPasswordHelperText, setConfirmPasswordHelperText] = useState('');
  const [currentPasswordError, setCurrentPasswordError] = useState(false);
  const [currentPasswordHelperText, setCurrentPasswordHelperText] = useState('');
  const [isPopup, setIsPopup] = useState(false)
  const { error } = useSelector(state => state.auth)

  const dispatch = useDispatch()


  const navigate = useNavigate();

  const changePassword = async () => {
    if (newPassword.trim() === '' || currentPassword.trim() === '' || confirmPassword === '') {
      setConfirmPasswordError(true);
      setConfirmPasswordHelperText(' ')
    } else if (newPassword !== confirmPassword) {
      setConfirmPasswordError(true);
      setConfirmPasswordHelperText('  ')
    } else if (!currentPassword.trim()) {
      setCurrentPasswordError(true);
      setCurrentPasswordHelperText(' ')
    }
    else {
      setConfirmPasswordError(false);
      setConfirmPasswordHelperText('')
      setCurrentPasswordError(false);
      setCurrentPasswordHelperText('')
      await dispatch(fetchChangePassword({ currentPassword, newPassword, confirmPassword }))

      setCurrentPassword('')
      setNewPassword('')
      setConfirmPassword('')

      togglePopup()
    }
  }

  const onChangeCurrentPassword = (e) => {
    setCurrentPassword(e.target.value);
    setCurrentPasswordError(false);
    setCurrentPasswordHelperText('')
  }

  const onChangeNewPassword = (e) => {
    setNewPassword(e.target.value);

  }

  const onChangeConfirmPassword = (e) => {
    setConfirmPassword(e.target.value);
    setConfirmPasswordError(false)
    setConfirmPasswordHelperText('')
  }

  const togglePopup = () => {
    setIsPopup(true);
    setTimeout(() => {
      setIsPopup(false)
    }, 3000)

  }

  return (
    <div className=' '>
      {isPopup && <PopupWindow setPopup={setIsPopup} text={error ? error.message : '  '} />}
      <div className=''>
        <h3>      WebMoney</h3>
      </div>
      <div >
        <div>
          <h4 className='text-lg font-bold mb-3'> </h4>
          <Input
            id="currentPassword"
            type="password"
            placeholder="  "
            onChange={onChangeCurrentPassword}
            value={currentPassword}
            error={currentPasswordError}
            helperText={currentPasswordHelperText}
            showPassword
          />
        </div>
        <div>
          <h4 className='text-lg font-bold mb-3'> </h4>
          <Input
            id="newPassword"
            // error={confirmEmailError}
            // helperText={confirmEmailHelperText}
            type="password"
            placeholder=" "
            onChange={onChangeNewPassword}
            value={newPassword}
            showPassword
          />
        </div>
        <div>
          <h4 className='text-lg font-bold mb-3'> </h4>
          <Input
            id="confirmPassword"
            type="password"
            onChange={onChangeConfirmPassword}
            placeholder=" "
            value={confirmPassword}
            error={onfirmPasswordError}
            helperText={onfirmPasswordHelperText}
            showPassword
          />
        </div>
        <ButtonGreen func={changePassword} title=" " />
      </div>
    </div>
  )
}
