import React, { useState, useEffect } from 'react'
import Close from '../../assets/images/close.svg'
import CloseLight from '../../assets/images/close-light.svg'
import { Input } from '../Input'
import { ButtonGreen } from '../ButtonGreen'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom'
import moment from 'moment'
import { links } from '../../links'
import { useDispatch, useSelector } from 'react-redux'
import { userIsAuth } from '../../store/slices/authSlice'
import { fetchGetcategories, fetchGetPaymentMethods } from '../../store/slices/categoriesSlice'
import Select from 'react-select'
import { Controller, useForm } from 'react-hook-form'
import axios from '../../axios'
import styles from './styles.scss'
import { clearOneOperation } from '../../store/slices/operationsSlice'
import { PopupWindow } from '../PopupWindow'
import { MainLoading } from '../MainLoading'

export const AddOperation = () => {
    const [typeOpeation, setTypeOperation] = useState('')
    const [typeCategory, setTypeCategory] = useState('')
    const [isPopup, setIsPopup] = useState(false)
    const [categoriesArr, setCategoriesArr] = useState([])
    const [paymentMethodsArr, setPaymentMethodsArr] = useState([])
    const { id } = useParams()
    const location = useLocation()
    const dispatch = useDispatch()
    const isAuth = useSelector(userIsAuth);
    const isLoading = useSelector(state => state.auth.isLoading)
    const { data: categories, isLoading: isLoadingCategories, dataPaymentMethods } = useSelector(state => state.categories)
    const navigate = useNavigate();

    const { register, handleSubmit, setValue, reset, control, formState: { errors, isValid } } = useForm({
        defaultValues: {
            title: '',
            amount: '',
            category: '',
            paymentMethod: '',
            type: location.pathname.includes('expense') ? 'expense' : 'revenue',
            date: ''
        },
        mode: 'onChange'
    });

    const title = register('title', { required: '     2    24 ', minLength: 2, maxLength: 24, })
    const type = register('type')
    const amount = register('amount', { required: '    99 999 999  99 ', min: 1.00, max: 999999999.99, })
    const date = register('date', { required: ' ' })

    const getCategories = async () => {
        if (location.pathname.includes('expense')) {
            await dispatch(fetchGetcategories({ category: 'expense' }));
            await dispatch(fetchGetPaymentMethods());
        } else {
            await dispatch(fetchGetcategories({ category: 'revenue' }));
        }
    }

    const onSubmit = async (values) => {
        try {
            id
                ? await axios.patch(`app/operation/${id}`, values)
                : await axios.post(`app/operation`, values)
            togglePopup()
        } catch (err) {
            console.log(err);
        } finally {
            if (id) {
                navigate('/app/home', { replace: true })
                dispatch(clearOneOperation())
            } else {
                reset()
            }
        }
    }
    const togglePopup = () => {
        setIsPopup(true);
        setTimeout(() => {
            setIsPopup(false)
        }, 3000)

    }

    const getOperation = async () => {
        if (id) {
            await axios.get(`app/operation/${id}`).then(({ data }) => {
                setValue('title', data.title)
                setValue('amount', data.amount)
                setValue('category', data.category)
                setValue('date', data.date)
                setValue('paymentMethod', data.paymentMethod)
            });
        }
    }

    const getValue = (value) => {
        return value ? categoriesArr.find(val => val.value === value) : ''
    }
    const getPaymentMethodValue = (value) => {
        return value ? paymentMethodsArr.find(val => val.value === value) : ''
    }

    useEffect(() => {
        if (!localStorage.getItem('token') && !isAuth) {
            navigate('/', { replace: true })
        }
        getOperation()
        getCategories();

        return () => {
            dispatch(clearOneOperation())
        }
    }, []);


    useEffect(() => {
        setCategoriesArr(categories ? categories.map(el => ({ value: el.title, label: el.title })) : [])
        setPaymentMethodsArr(dataPaymentMethods ? dataPaymentMethods.map(el => ({ value: el.title, label: <div className='flex gap-3 items-center'><img className='w-12' src={`https://web-money-backend.vercel.app/${el.imageUrl}`} alt={el.title} /><h3>{el.title}</h3></div> })) : [])
    }, [categories, dataPaymentMethods])

    useEffect(() => {
        if (location.pathname.includes('expense')) {
            if (id) {
                setTypeOperation('')
                setTypeCategory('')
            } else {
                setTypeOperation('')
                setTypeCategory('')
            }
        } else {
            if (id) {
                setTypeOperation('')
                setTypeCategory('')
            } else {
                setTypeOperation('')
                setTypeCategory('')
            }
        }
    }, [])

    //     
    const minDate = moment().add(-11, 'M').startOf('M').format('YYYY-MM-DD');
    const maxDate = moment().format('YYYY-MM-DD')



    if ((isLoading && !isAuth) || isLoadingCategories) {
        return <div className='w-full h-screen flex justify-center items-center'><MainLoading /></div>
    }

    return (
        <>
            {isPopup && <PopupWindow setPopup={setIsPopup} text={id ? ' ' : ' '} />}
            <div className='w-full pt-0 md:pt-14 md:max-w-[864px] mx-auto py-2 px-3 fixed md:static overflow-auto bg-background dark:bg-white md:bg-transparent h-screen z-[888] '>
                <div className='flex py-2 justify-between md:justify-center  items-center mb-3'>
                    <h3 className='font-bold text-lg'>{typeOpeation} {typeCategory}</h3>
                    <Link to={links.home} className='w-8 h-8 cursor-pointer md:hidden'>
                        {localStorage.getItem('theme') === 'dark'
                            ? <img src={Close} alt="close" className='w-8 h-8 cursor-pointer md:hidden' />
                            : <img src={CloseLight} alt="close" className='w-8 h-8 cursor-pointer md:hidden' />

                        }
                    </Link>
                </div>
                <form
                    onSubmit={handleSubmit(onSubmit)}
                    className={styles.form}
                >
                    <div className='flex-1'>
                        <Input
                            type="hidden"
                            onChange={type.onChange}
                            name={type.name}
                            ref={type.ref}
                            error={Boolean(errors.type)}
                            helperText={errors.type?.message}

                        />

                        <Input
                            id='nameOperation'
                            maxLength={24}
                            placeholder=' ...'
                            onChange={title.onChange}
                            name={title.name}
                            ref={title.ref}
                            error={Boolean(errors.title)}
                            helperText={errors.title?.message}
                        />
                        <Input
                            id='nameOperation'
                            type='number'
                            placeholder=''
                            onChange={amount.onChange}
                            name={amount.name}
                            ref={amount.ref}
                            error={Boolean(errors.amount)}
                            helperText={errors.amount?.message}

                        />

                        {location.pathname.includes('expense') && <Controller
                            control={control}

                            name='paymentMethod'
                            render={({ field: { onChange, value }, fieldState: { error } }) => <Select
                                options={
                                    [{ value: '', label: <div className='flex gap-3 items-center'><h3> </h3></div> }, ...paymentMethodsArr]
                                }
                                placeholder="  "
                                classNamePrefix={localStorage.getItem('theme') === 'dark' ? 'custom-select-dark' : 'custom-select'}


                                value={getPaymentMethodValue(value)}
                                onChange={(newValue) => onChange(newValue.value)}
                            />}
                        />}

                        <Controller
                            control={control}
                            rules={{
                                required: ' '
                            }}
                            name='category'
                            render={({ field: { onChange, value }, fieldState: { error } }) => <Select
                                options={categoriesArr}
                                placeholder=" "
                                classNamePrefix={localStorage.getItem('theme') === 'dark' ? 'custom-select-dark' : 'custom-select'}
                                value={getValue(value)}
                                onChange={(newValue) => onChange(newValue.value)}
                            />}
                        />

                        <Input
                            id='nameOperation'
                            type='date'
                            min={minDate}
                            max={maxDate}
                            onChange={date.onChange}
                            name={date.name}
                            ref={date.ref}
                            error={Boolean(errors.date)}
                            helperText={errors.date?.message}

                        />
                    </div>

                    <div className='flex flex-col gap-3 '>
                        <ButtonGreen
                            disabled={isValid}
                            type="submit"
                            title={`${typeOpeation} ${typeCategory}`}
                            cn='w-full '
                        />

                        <Link to={links.home} className='py-3 px-3 border bg-gradient-to-r from-darkBlack to-darkBlack dark:text-textPrime text-center hover:from-darkBlack hover:to-bggBottom transition-all border-white rounded-xl'></Link>
                    </div>
                </form>


            </div >

        </>
    )
}
